'use client';

import React, { useState } from 'react';
import SidebarItem from './SidebarItem';
import UserProfile from './UserProfile';
import ThemeSwitcher from '../Layout/ThemeSwitcher';
import LogoutButton from './LogoutButton';
import Image from 'next/image';
import { useTheme } from 'next-themes';

const Sidebar: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const { theme } = useTheme();

  const toggleSidebar = () => setIsOpen(!isOpen);

  const iconPath = (filename: string) =>
    `/assets/${theme === 'dark' ? 'dark' : 'light'}/${filename}`;

  return (
    <>
      {/* üîπ Mobile Header */}
      <header
        className={`md:hidden fixed top-0 left-0 right-0 z-50 flex items-center justify-between bg-white dark:bg-neutral-900 px-4 py-3 shadow-md transition-opacity duration-300 ${
          isOpen ? 'opacity-0 pointer-events-none' : 'opacity-100'
        }`}
      >
        <Image src={iconPath('logo.svg')} alt="Logo" width={32} height={32} />
        <button onClick={toggleSidebar} title="Abrir men√∫ lateral">
          <Image src={iconPath('hamburger.svg')} alt="Men√∫" width={24} height={24} />
        </button>
      </header>

      {/* üîπ Overlay */}
      {isOpen && (
        <div
          onClick={toggleSidebar}
          className="fixed inset-0 bg-black bg-opacity-40 z-40 md:hidden"
        />
      )}

      {/* üîπ Sidebar */}
      <aside
        className={`
          sidebar-custom fixed top-0 left-0 h-screen w-64 z-50
          flex flex-col
          transform transition-transform duration-300
          ${isOpen ? 'translate-x-0' : '-translate-x-full'}
          md:translate-x-0 md:static md:block
        `}
      >
        {/* Contenido principal */}
        <div className="p-4 flex flex-col flex-grow overflow-y-auto">
          {/* Logo desktop */}
          <div className="mb-8 hidden md:flex items-center justify-center">
            <Image src={iconPath('logo.svg')} alt="Logo" width={40} height={40} />
          </div>

          <nav className="flex flex-col space-y-4">
            <SidebarItem iconName="progress" label="Tu Progreso" href="/" />
            <SidebarItem iconName="resources" label="Recursos Adicionales" href="/resources" />
            <SidebarItem iconName="certifications" label="Certificaciones" href="/certificates" />
            <SidebarItem iconName="support" label="Soporte y Ayuda" href="/support" />
            <div className="mt-6">
              <ThemeSwitcher />
            </div>
          </nav>
        </div>

        {/* Footer: UserProfile + Logout */}
        <div className="p-4 border-t border-gray-200 dark:border-neutral-700 flex flex-col items-center gap-2">

          <div className="w-full flex justify-center pt-2">
            <LogoutButton />
          </div>
        </div>
      </aside>
    </>
  );
};

export default Sidebar;
